@font-face {
  font-family: "PT Mono";
  src: url(../fonts/PTMono.ttf);
  font-weight: 400; }

@font-face {
  font-family: "Roboto Condensed";
  src: url(../fonts/RobotoCondensed-Regular.ttf);
  font-weight: 400; }

@font-face {
  font-family: "Roboto Condensed";
  src: url(../fonts/RobotoCondensed-Bold.ttf);
  font-weight: 600; }

html {
  box-sizing: border-box;
  min-width: 320px; }

*,
::before,
::after {
  box-sizing: inherit; }

img {
  display: inline-block;
  margin: 0 auto;
  max-width: 100%; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

.header {
  background-color: #1B2931; }

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  padding-top: 10px; }
  @media (min-width: 768px) {
    .header__wrapper {
      padding-right: 25px;
      padding-left: 25px; } }
  @media (min-width: 1024px) {
    .header__wrapper {
      padding-right: 40px;
      padding-left: 40px; } }

.logo {
  margin-right: auto; }

.button {
  font-family: "Roboto Condensed", Arial, sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border-radius: 5px; }
  .button--callback {
    font-size: 14px;
    font-weight: 600;
    border: 1px solid #ffffff;
    color: #ffffff;
    background-color: transparent;
    display: none; }
  .button--lead {
    background-color: #00B2FF;
    font-size: 16px;
    font-weight: 600;
    color: #ffffff;
    border: none;
    width: 100%;
    padding-top: 20px;
    padding-bottom: 18px; }
  @media (min-width: 1024px) {
    .button {
      display: block;
      padding-top: 14px;
      padding-bottom: 11px;
      width: 180px; } }

.service {
  display: flex;
  align-items: center;
  list-style: none; }

@media (min-width: 1024px) {
  .service-position {
    margin-right: 40px; } }

.service__link {
  width: 26px;
  height: 26px;
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #ffffff;
  font-family: "PT Mono", Arial, sans-serif;
  font-size: 0;
  background-repeat: no-repeat;
  background-position: center center; }
  .service__link--email {
    background-image: url("../img/envelope.png");
    margin-right: 23px; }
  .service__link--phone {
    background-image: url("../img/phone-call.png"); }
  @media (min-width: 1024px) {
    .service__link {
      width: 220px;
      padding-left: 41px;
      font-size: 16px;
      background-position: left center; } }

.lead {
  background-color: #1B2931;
  padding-top: 25px;
  padding-bottom: 70px; }

.lead__wrapper {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto; }
  @media (min-width: 768px) {
    .lead__wrapper {
      padding-right: 25px;
      padding-left: 25px; } }
  @media (min-width: 1024px) {
    .lead__wrapper {
      padding-right: 40px;
      padding-left: 40px; } }

.lead__title {
  font-family: "Roboto Condensed", Arial, sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  color: #ffffff;
  letter-spacing: 0.07em;
  font-size: 40px;
  margin-top: -28px; }

.lead__subtitle {
  position: relative;
  font-family: "PT Mono", Arial, sans-serif;
  font-size: 16px;
  color: #ffffff;
  line-height: 1.7em;
  padding-left: 48px; }
  .lead__subtitle:before {
    display: block;
    position: absolute;
    content: "";
    height: 2px;
    width: 33px;
    background-color: #ffffff;
    top: 16px;
    left: 0; }

.lead__subtitle-position {
  margin-bottom: 32px; }
